< <template>
  <div>
       <div v-if="state" class="div">
      <el-form
        :label-position="labelPosition"
        label-width="80px"
        :model="formLabelAlign"
        class="div1"
      >
        <el-form-item label="角色编号：" label-width="100px">
          <el-input v-model="formLabelAlign.id"></el-input>
        </el-form-item> 
        <el-form-item label="登录名称：" label-width="100px">
          <el-input v-model="formLabelAlign.nickName"></el-input>
        </el-form-item>
        <el-form-item label="角色名称：" label-width="100px">
          <el-input v-model="formLabelAlign.name" ></el-input>
        </el-form-item>
        <el-form-item label="修改时间：" label-width="100px">
          <el-input v-model="formLabelAlign.createTime"></el-input>
        </el-form-item>
         <div class="butcal">
            <el-button type="primary" @click="query">确定</el-button>
            <el-button @click="cancel">取消</el-button>
         </div>
      </el-form>
    </div>
  <el-row class="userIndexBtn">
    <el-button type="primary">新增</el-button>
    <el-button type="success">修改</el-button>
    <el-button type="danger">删除</el-button>
  </el-row>

  <el-table :data="tableData" class="userIndex">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column label="角色编号" width="80px">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span>{{ scope.row.id }}</span>
      </template>
    </el-table-column>
    <el-table-column prop="nickName" label="登录名称" width="110px">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span >{{ scope.row.nickName }}</span>
      </template>
    </el-table-column>
    <el-table-column label="角色名称" width="110px">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span >{{ scope.row.name }}</span>
      </template>
    </el-table-column>
    <el-table-column label="角色状态" width="100px">
      <el-switch
        v-model="value"
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch>
    </el-table-column>
    <el-table-column prop="time" label="创建时间" width="160px">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span>{{ scope.row.createTime }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作" style="margin-left:20px">
      <template slot-scope="scope">
        <el-button 
          size="mini"
          @click="handleEdit(scope.$index,tableData)"
          type="success"
          >编辑</el-button
        >
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button
        >
      </template>
    </el-table-column>
  </el-table>
 </div>
</template> 




<script>
import { getUserRole } from "@/moke/getData";
export default {
  data() {
    return {
      tableData: [],
      value: true,
      state: false,
      labelPosition: "left",
      formLabelAlign: {},
      formLabelWidth: "120px",
      index:''
    };
  },
  methods: {
     handleEdit(index,tableData) {
      this.state = true;
      this.index = index
      console.log(index, tableData);
      this.formLabelAlign=this.tableData[index]
      console.log(this.tableData);
    },
    query(){
      this.tableData[this.index] = this.formLabelAlign
      this.state = false
      console.log(this.formLabelAlign.phone);
    },
    cancel(){
      this.state = false
    },
     handleDelete(index, row) {
      this.tableData.splice(index, 1);
    },
    async getMockList() {
      try {
        const result = await getUserRole();
        console.log(result);
        if (result.data.code == 0) {
          this.tableData = result.data.data.data;
        }
        //console.log(result.data.data.data);
      } catch (error) {
        console.log(error);
      }
    },
  },
  mounted() {
    this.getMockList();
  },
};
</script>

<style scoped>
.userIndexBtn {
  width: 100%;
  height: 100%;
  border-radius: 6px 6px 0 0;
  background-color: #fff;
  padding: 20px 20px;
  border-bottom: 1px solid rgb(239, 238, 238);
}
.userIndex {
  width: 100%;
  height: 100%;
  border-radius: 0 0 6px 6px;
}
.el-icon-time:before {
  content: "";
}
.div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9;
}
.div1 {
 width: 500px;
 padding:30px 30px;
 background-color: #fff;
 position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 5px;
}
.butcal{
  display: flex;
  justify-content: flex-end;
}
</style>